.dialogs {
    display: flex;
    justify-content: space-around;
}


.dialogs__item a {
    color: #1c1e65;
    font-size: 21px;
    font-weight: 400;
    line-height: 1;
}

.dialogs__header {
    margin-bottom: 10px;
    height: 200px;
}

.send {
    display: flex;
    justify-content: center;
    align-items: stretch;
    margin-top: 20px;
}

